body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Nav ul {
  display: -webkit-flex;
  display: flex;
  padding: 0;
  margin: 0;
}

.Nav ul li {
  list-style-type: none;
  padding: 10px;
}

/* SEARCHBOX COMP */
.SearchBox {
  padding: 0;
  margin: 0 auto;
  position: relative;
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 0px 20px -5px rgba(159, 58, 58, .7);
}

.SearchBox input {
  border: none;
  padding: 10px 20px;
  -webkit-flex: 1 1;
          flex: 1 1;

}

.SearchBox input[placeholder] {
  font-size: inherit;
}


.search-btn {
  /* position: absolute; */
  /* top: 50%;
  right: 0; */
  /* transform: translateY(-50%); */
  background: none;
  border: none;
  color: rgb(7, 111, 170);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
}

.SearchBox .search-btn:hover {
  /* border: 1px solid rgba(7, 111, 170,0.5); */
  box-shadow: 0 5px 10px -5px rgba(0,0,0,0.2);
  cursor: pointer;
}

.SearchBox .search-btn:hover #lupita-svg {
  cursor: pointer;
  fill: #fff;
  background-color: rgba(159, 58, 58, 1);
}

.SearchBox #lupita-svg {
  padding: 10px;
  height: 50px;
  width: 80px;
  background-color: rgba(159, 58, 58, .9);
  fill: #ddd;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  pointer-events: none;
}

@media  (max-width: 500px) {
  .SearchBox input {
    padding: 10px;
  }
  .SearchBox #lupita-svg {
    height: 48px;
    width: auto;
  }
}

.Card {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 10px 10px -10px rgba(159, 58, 58, 1);
  border-radius: 10px;
  margin-bottom: 20px;
}

.book-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.book-title {
  margin: 0 10px;
}

.book-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.book-img {
  padding: 10px;
  /* background: #999; */
  min-width: 200px;
  height: 100%;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.book-more {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
  -webkit-align-self: stretch;
          align-self: stretch;
}

.book-subtext {
  margin: 0;
}

.book-desc {
  -webkit-flex: 1 1;
          flex: 1 1;
}

@media (max-width: 500px) {
  .book-info {
    display: block;
  }

  .book-img {
    max-width: 100%;
  }

  .book-more {
  }
}

*, *::after, *::before {
  box-sizing: border-box;
}

.App {
  margin: 0 auto;
  padding: 20px;
  max-width: 900px;
}

.App-header {
  padding: 0 0 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App-header h1 {
  margin: 0;
  /* color: rgba(159, 58, 58, 1); */
  border-bottom: 2px solid rgba(159, 58, 58, 1);
}


.Default {
  text-align: center;
}

